import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Unstable_Grid2';


//ICONS
import IconButton from '@mui/material/IconButton';
import CheckOutlinedIcon from '@mui/icons-material/CheckOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import ModeEditOutlineOutlinedIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import { useContext, useState } from 'react';
import { TodosContext } from '../contexts/todosContext';


// Dialog Imports

import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import TextField from '@mui/material/TextField';



export default function Todo({ todo }) {
  const [showDeleteDialog, setShowDeleteDialog] = useState(false)
  const [showUpdateDialog, setShowUpdateDialog] = useState(false)
  const [updatedTodo, setUpdatedTodo] = useState({ title: todo.title, details: todo.details })
  const { todos, setTodos } = useContext(TodosContext);


  // Event Handlers
  function handelCheckClick() {
    const updatedTodos = todos.map((t) => {
      if (t.id === todo.id) {
        t.isDone = !t.isDone
      };
      return t;
    });
    setTodos(updatedTodos);
    localStorage.setItem("todos", JSON.stringify(updatedTodos));
  }

  function handleDeleteClick() {
    setShowDeleteDialog(true)
  }
  function handleUpdateClick() {
    setShowUpdateDialog(true)
  }

  function handleDeleteDialogClose() {
    setShowDeleteDialog(false)
  }

  function handleUpdateClose() {
    setShowUpdateDialog(false)
  }

  function handleDeleteConfirm() {
    const updatedTodos = todos.filter((t) => t.id !== todo.id);
    setTodos(updatedTodos);
    setShowDeleteDialog(false)
    localStorage.setItem("todos", JSON.stringify(updatedTodos));
  }
  function handleUpdateConfirm() {
    const updatedTodos = todos.map((t) => {
      if (t.id === todo.id) {
        t.title = updatedTodo.title
        t.details = updatedTodo.details
      };
      return t;
    });
    setTodos(updatedTodos);
    setShowUpdateDialog(false)
    localStorage.setItem("todos", JSON.stringify(updatedTodos));
  }


  // ==== Event Handlers

  return (
    <>

      {/* Delete Dialog */}
      <Dialog
        onClose={handleDeleteDialogClose}
        open={showDeleteDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Are you sure you want to delete the task?
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            You cannot undo the deletion if you choose the button: (Delete).
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDeleteDialogClose}>Cancel</Button>
          <Button autoFocus onClick={handleDeleteConfirm}>
            Confirm deletion
          </Button>
        </DialogActions>
      </Dialog>
      {/* ==== Delete Dialog */}



      {/* Update Dialog */}
      <Dialog
        onClose={handleUpdateClose}
        open={showUpdateDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Edit Task
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            required
            margin="dense"
            id="name"
            name="email"
            label="Task Title"
            fullWidth
            variant="standard"
            value={updatedTodo.title}
            onChange={(e) => setUpdatedTodo({ ...updatedTodo, title: e.target.value })}
          />
          <TextField
            autoFocus
            required
            margin="dense"
            id="name"
            name="email"
            label="Details"
            fullWidth
            variant="standard"
            value={updatedTodo.details}
            onChange={(e) => setUpdatedTodo({ ...updatedTodo, details: e.target.value })}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleUpdateClose}>Cancel</Button>
          <Button autoFocus onClick={handleUpdateConfirm}>
            Update
          </Button>
        </DialogActions>
      </Dialog>
      {/* ==== Update Dialog */}

      <Card
        className='todoCard'
        sx={{
          minWidth: 275,
          background: "#283593",
          color: "white",
          marginTop: 5
        }}>
        <CardContent>
          <Grid container spacing={2}>
            <Grid xs={8}>
              <Typography variant='h5'
                sx={{ textAlign: "left", textDecoration: todo.isDone ? "line-through" : "none"}}>
                {todo.title}
              </Typography>
              <Typography variant='h6' sx={{ textAlign: "left" }}>
                {todo.details}
              </Typography>
            </Grid>
            {/* Action Button */}
            <Grid
              xs={4}
              display="flex"
              justifyContent="space-around"
              alignItems="center"
            >
              {/* Check Icon Button */}
              <IconButton
                onClick={() => {
                  handelCheckClick();
                }}
                className="iconButton"
                aria-label="Check"
                style={{
                  color: todo.isDone ? "white" : "#8bc34a",
                  background: todo.isDone ? "#8bc34a" : "white",
                  border: "solid #8bc34a 3px"
                }}>
                <CheckOutlinedIcon />
              </IconButton>
              {/* ====Check Icon Button */}

              {/* Edit Icon Button */}
              <IconButton
                onClick={handleUpdateClick}
                className="iconButton"
                aria-label="Edit"
                style={{
                  color: "#1769aa",
                  background: "white",
                  border: "solid #1769aa 3px"
                }}>
                <ModeEditOutlineOutlinedIcon />
              </IconButton>
              {/* ==== Edit Icon Button */}

              {/* Delete Icon Button */}
              <IconButton
                className="iconButton"
                aria-label="delete"
                style={{
                  color: "#b23c17",
                  background: "white",
                  border: "solid #b23c17 3px"
                }}
                onClick={handleDeleteClick}
              >
                <DeleteOutlineOutlinedIcon />
              </IconButton>
              {/* ==== Delete Icon Button */}

            </Grid>
          </Grid>
          {/* ==== Action Button */}

          <Divider />
        </CardContent>
      </Card>
    </>
  );
}