import './App.css';
import TodoList from './components/TodoList';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { TodosContext } from './contexts/todosContext';
import { v4 as uuidv4 } from 'uuid';
import { useState } from 'react';


const theme = createTheme({
  typography: {
    fontFamily: ['Alexandria']
  },
  palette: {
    primary: {
      main: '#ad1457',
    }
  },
});

const initialTodos = [
  { id: uuidv4(), title: "Learn React", details: "Learn React First Chapter", isDone: false },
  { id: uuidv4(), title: "Learn Redux", details: "Learn Redux Second Chapter", isDone: false },
  { id: uuidv4(), title: "Learn Next", details: "Learn Next Third Chapter", isDone: false }
];

function App() {
  const [todos, setTodos] = useState(initialTodos);
  return (
    <ThemeProvider theme={theme}>
      <div className="App">
      <TodosContext.Provider value={{todos, setTodos}}>
          <TodoList />
        </TodosContext.Provider>
      </div>
    </ThemeProvider>
  );
}

export default App;
