import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Divider from '@mui/material/Divider';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Grid from '@mui/material/Unstable_Grid2';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';


// Components
import Todo from "./Todo"

// OTHERS
import { v4 as uuidv4 } from 'uuid';
import { TodosContext } from '../contexts/todosContext';
import { useContext, useState, useEffect } from 'react';



export default function TodoList() {

  const { todos, setTodos } = useContext(TodosContext);

  const [titleInput, setTitleInput] = useState("");

  const [displayedTodosType, setDisplayedTodosType] = useState("all");


  // filtration Arrays 

  const completedTodos = todos.filter((t) => t.isDone === true);

  const unCompletedTodos = todos.filter((t) => t.isDone === false);


  let todosToBeRendered = todos

  if (displayedTodosType === "Completed") {
    todosToBeRendered = completedTodos
  } else if (displayedTodosType === "non-Completed") {
    todosToBeRendered = unCompletedTodos
  } else {
    todosToBeRendered = todos
  }



  const todosJsx = todosToBeRendered.map((t) => {
    return <Todo key={t.id}
      todo={t}
    />;
  });


  useEffect(() => {
    const storedTodos = localStorage.getItem("todos");
    const parsedTodos = storedTodos ? JSON.parse(storedTodos) : [];
    setTodos(parsedTodos);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  function changeDisplayedType(e) {
    setDisplayedTodosType(e.target.value);
  }


  function handleAddClick() {
    const newTodo = {
      id: uuidv4(),
      title: titleInput,
      details: "",
      isDone: false
    };

    const updatedTodos = [...todos, newTodo]
    setTodos(updatedTodos);
    localStorage.setItem("todos", JSON.stringify(updatedTodos));
    setTitleInput("");
  }



  return (
    <Container maxWidth="sm">
      <Card sx={{ minWidth: 275, maxHeight: "80vh", overflow: "scroll" }}>
        <CardContent>
          <Typography variant='h2' sx={{ fontWeight: "bold" }}>
            Tasks
          </Typography>
          <Divider />
          {/* Filter Buttons */}
          <ToggleButtonGroup
            style={{ marginTop: "30px" }}
            value={displayedTodosType}
            exclusive
            onChange={changeDisplayedType}
            aria-label="text alignment"
            color='primary'
          >
            <ToggleButton value="all">
              All tasks
            </ToggleButton>
            <ToggleButton value="Completed">
              Completed tasks
            </ToggleButton>
            <ToggleButton value="non-Completed" >
              incomplete tasks
            </ToggleButton>
          </ToggleButtonGroup>
          {/* ==== Filter Buttons */}
          {/* All ToDos */}
          {todosJsx}
          {/* ==== All ToDos */}

          {/* Input + Add Button */}
          <Grid container sx={{ marginTop: "20px" }} spacing={2}>
            <Grid
              xs={8}
              display="flex"
              justifyContent="space-around"
              alignItems="center"
            >
              <TextField
                sx={{ width: "100%" }}
                id="outlined-basic"
                label="Task Title"
                variant="outlined"
                value={titleInput}
                onChange={(e) => setTitleInput(e.target.value)}
              />
            </Grid>


            <Grid
              xs={4}
              display="flex"
              justifyContent="space-around"
              alignItems="center"

            >
              <Button
                sx={{ width: "100%", height: "100%" }}
                variant="contained"
                onClick={() => {
                  handleAddClick();
                }}
                disabled={
                  titleInput === ""
                    ? true
                    : false
                }
              >Add</Button>
            </Grid>
          </Grid>
          {/* ====Input + Add Button */}
        </CardContent>
      </Card>
    </Container>
  );
}
